import playerIcon2 from '../../assets/playerIcons/player_2.png';
import playerIcon3 from '../../assets/playerIcons/player_3.png';
import playerIcon4 from '../../assets/playerIcons/player_4.png';
import playerIcon5 from '../../assets/playerIcons/player_5.png';
import playerIcon6 from '../../assets/playerIcons/player_6.png';
import playerIcon7 from '../../assets/playerIcons/player_7.png';
import playerIcon8 from '../../assets/playerIcons/player_8.png';
import playerIcon9 from '../../assets/playerIcons/player_9.png';
import playerIcon10 from '../../assets/playerIcons/player_10.png';

import humanIcon1 from '../../assets/characterIcons/human_1.png';
import humanIcon2 from '../../assets/characterIcons/human_2.png';
import dwarfIcon1 from '../../assets/characterIcons/dwarf_1.png';
import dwarfIcon2 from '../../assets/characterIcons/dwarf_2.png';
import elfIcon1 from '../../assets/characterIcons/elf_1.png';
import elfIcon2 from '../../assets/characterIcons/elf_2.png';
import halfOrcIcon1 from '../../assets/characterIcons/halfOrc_1.png';
import halfOrcIcon2 from '../../assets/characterIcons/halfOrc_2.png';
import halfElfIcon1 from '../../assets/characterIcons/halfElf_1.png';
import halfElfIcon2 from '../../assets/characterIcons/halfElf_2.png';
import halflingIcon1 from '../../assets/characterIcons/halfling_1.png';
import halflingIcon2 from '../../assets/characterIcons/halfling_2.png';


import mainMenuBG from '../../assets/fullscreen/title.png'
import defaultBg from '../../assets/fullscreen/not-implemented.png'
import campaignBg from '../../assets/fullscreen/campaign.png'

import cultOfTheDeep1 from '../../assets/campaign/cult_of_the_deep_1.png';
import trolls1 from '../../assets/campaign/trolls_1.png';
import millstoneMystery1 from '../../assets/campaign/millstone_mystery.png';

// Create a map of icon paths:
const imageMap: { [key: string]: string } = {
    // Player Icons
    player_0: playerIcon0,
    player_1: playerIcon1,
    player_2: playerIcon2,
    player_3: playerIcon3,
    player_4: playerIcon4,
    player_5: playerIcon5,
    player_6: playerIcon6,
    player_7: playerIcon7,
    player_8: playerIcon8,
    player_9: playerIcon9,
    player_10: playerIcon10,
    // Character Icons
    human_1: humanIcon1,
    human_2: humanIcon2,
    dwarf_1: dwarfIcon1,
    dwarf_2: dwarfIcon2,
    elf_1: elfIcon1,
    elf_2: elfIcon2,
    halfOrc_1: halfOrcIcon1,
    halfOrc_2: halfOrcIcon2,
    halfElf_1: halfElfIcon1,
    halfElf_2: halfElfIcon2,
    halfling_1: halflingIcon1,
    halfling_2: halflingIcon2, 
     
   //fullscreens
    main_menu: mainMenuBG,
    default_bg: defaultBg,
    campaign_bg: campaignBg,

    // campaignIcons
    cult_of_the_deep_1: cultOfTheDeep1,
    trolls_1: trolls1,
    millstone_mystery_1: millstoneMystery1,

}


interface ImageProps {
    imageId: string | undefined;
  }

const CImage: React.FC<ImageProps> = ({ imageId }) => {
    const path = imageMap[imageId || ''];

    return (
        <img src={path} alt="Icon" className="cimage" />
    );
}

export default CImage