import React, { useEffect } from 'react';
import { Campaign } from '../models';
import Cimage from './UI/cimage';
import './campaignDisplay.scss';
import CampaignDisplayBody from './campaign-display-body';



interface CampaignDisplayProps {
  campaign: Campaign;
  onSelect: () => void;
  collapsed: boolean;
  onAccept: () => void; 
  onCancel: () => void;
}

const CampaignDisplay: React.FC<CampaignDisplayProps> = ({ campaign, onSelect, collapsed, onAccept, onCancel }) => {

  useEffect(() => {
    if(!campaign.processedEncounters) {
      campaign.processFunction();
    }
  }, []);

  return (
    <>
      {collapsed ? (
        <div className='campaign-display collapsed pill-shaped' onClick={onSelect}>
          <div className='campaign-display-header'>
            <div className="campaign-icon">
              <Cimage imageId={campaign.icon} />
            </div>
            <div className="campaign-info">
              <div className="campaign-name"><strong>{campaign.name}</strong></div>
              <div className="campaign-description">{campaign.description}</div>
            </div>
            <div className="campaign-extra">
              Blah
            </div>
          </div>
        </div>
      ) : (
        <div className='campaign-display full'>
          <div className='campaign-display-header'>
            <div className="campaign-icon">
              <Cimage imageId={campaign.icon} />
            </div>
            <div className="campaign-info">
              <div className="campaign-name"><strong>{campaign.name}</strong></div>
              <div className="campaign-description">{campaign.description}</div>
            </div>
            <div className="campaign-extra">
              Blah
            </div>
          </div>
         
            <CampaignDisplayBody processedEncounters={campaign.processedEncounters} />
          
          <div className='campaign-display-footer'>
            <div className="ui-actions">
                <button className="campaign-button accept" onClick={onAccept}>Accept</button>
                <button className="campaign-button cancel" onClick={onCancel}>Cancel</button>
            </div>
          </div>
        </div>
      )}
    </>
  )
};

export default CampaignDisplay;