import { CLASS, RACE } from "../consts/general";
import { lerpColor } from "../utils/utils";


type ColorLerp = {
  start: string;
  end: string;
  current: string;
  duration: number;
  lifetime: number;
}

class UiManager {

  lerpBank: { [key: string]: ColorLerp } = {}
  
  constructor() {
    this.init();
  }


  startColorLerp(name: string, start: string, end: string, duration: number) {
    if(start === '') {
      if(this.lerpBank[name]){
        start = this.lerpBank[name].current
      } else {
        start = end;
        duration = 1;
      }
    } else if(start === 'override') {
        start = end;
        duration = 1;
    }
    
    const lerp: ColorLerp = {
      start,
      end,
      duration,
      lifetime: 0,
      current: start
    }
    this.lerpBank[name] = lerp;
  }

  getColorLerp(name: string) {
    return this.lerpBank[name]?.current || 'pink';
  }

 
  showAcceptCampaignModal(campaignID: number) {
    console.log('showing accept campaign modal...');
  }


  init() {
    console.log('init uiManager...');
    // Logic to guide players through the graph
  }

  update(timeDt: number) {
    this.updateColorLerps(timeDt);
  }

  updateColorLerps(timeDt: number) {
    for (const key in this.lerpBank) {
      const lerp = this.lerpBank[key];
      if(lerp.lifetime <= lerp.duration) {
        lerp.lifetime = Math.min(lerp.lifetime + timeDt, lerp.duration);
        const color = lerpColor(lerp.start, lerp.end, lerp.duration, lerp.lifetime);
        lerp.current = color;
      }
    }
  }



  // Other methods to manage sessions
}

export default UiManager;