import React, { useEffect, useState } from 'react';
import './characterCreation.scss';
import PlayerCard from './playerCard'; // Adjust the import path as needed
import CharacterCard from './characterCard'; // Adjust the import path as needed
import { useAppManager, useCharacterManager, usePlayerManager } from './managerContexts';
import { Character, Player } from '../models';
import { APP_STATE } from '../consts/states';

interface CharacterCreationProps { 

}

const CharacterCreation: React.FC<CharacterCreationProps> = ({}) => {

  const playerManager = usePlayerManager()
  const characterManager = useCharacterManager();
  const appManager = useAppManager();

  const [selectedCharacter, setSelectedCharacter] = useState<number| undefined>(undefined);
  const [players, setPlayers] = useState<Player[]>(playerManager.getPlayers());
  const [availableCharacters] = useState<Character[]>(characterManager.getCharacterReferences());
  const [update, setUpdate] = useState(false);



  const toggleSelectCharacter = (character: number) => {
    if(selectedCharacter === character){
      setSelectedCharacter(undefined);
      characterManager.setSelectedCharacters([]);
      return;
    }
  
    setSelectedCharacter(character);
    characterManager.setSelectedCharacters([character]);

  };

  const forceRender = () => {
    setUpdate(!update);
  }

  const assignOrSwap = (player: Player) => {
    if(selectedCharacter === undefined)
      return;
    playerManager.toggleSinglePlayerForCharacter(player, selectedCharacter);
    setSelectedCharacter(undefined);
    setUpdate(!update);
  }
  
  const isAssigned = (characterIndex: number) => {
    return players.some((player) => player.characters.includes(characterIndex));
  }

  
  const acceptCharacters = () => {
    appManager.state = APP_STATE.MAIN_MENU;
  }
  
  const getOverlayStyle = (player: Player) => {
  if(selectedCharacter === undefined)
    return 'none';
  if(player.characters.includes(selectedCharacter)){
    return 'unassign';
  } else {
    return 'assign';
  }
  }

return (
    <div className="character-creation-container">
      <div className="player-list-container">
        <div className="accept-character-button" onClick={() => acceptCharacters()}>
            <div className="accept-text">
              Accept Characters
            </div>
          
        </div>
        <div className="player-list">
          {players.map((player) => (
            <PlayerCard 
              key={player.id}
              player={player}
              isSelected={false}
              onSelect={() => assignOrSwap(player)}
              overlayStyle={getOverlayStyle(player)}
              showSchedule={false}
              showCharacters={true}
              parentUpdate={forceRender}
            >
            </PlayerCard>
          ))}
        </div>
      </div>
      <div className="character-assignment">
        <div className="character-grid">
          {availableCharacters.map((character, index) => (
            <CharacterCard
              key={character.id}
              character={character}
              isSelected={selectedCharacter === index}
              onSelect={() => toggleSelectCharacter(index)}
              showAbilities={true}
              isAssigned={isAssigned(index)}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default CharacterCreation;
