const linkCampaignNodes = () => {
    // SAGEWOOD_TAVERN
    NODES.SAGEWOOD_TAVERN.pathNodes.push(NODES.BREWERY_FIGHT);
    NODES.SAGEWOOD_TAVERN.pathNodes.push(NODES.BY_THE_LAKE);

    // BREWERY_FIGHT
    NODES.BREWERY_FIGHT.pathNodes.push(NODES.BREWERY_EXPLORATION);
    NODES.BREWERY_FIGHT.pathNodes.push(NODES.BY_THE_LAKE);

    // BY_THE_LAKE
    NODES.BY_THE_LAKE.pathNodes.push(NODES.LAKESIDE_CLIFFS);

    // LAKESIDE_CLIFFS
    NODES.LAKESIDE_CLIFFS.pathNodes.push(NODES.UNDER_THE_WELL);

   // cultOfTheDeep.processedEncounters = CampaignManager.processEncounters(cultOfTheDeep);
    cultOfTheDeep.processedEncounters =  [[NODES.SAGEWOOD_TAVERN],[NODES.BREWERY_FIGHT, NODES.BY_THE_LAKE],[NODES.BREWERY_EXPLORATION, NODES.LAKESIDE_CLIFFS],[NODES.UNDER_THE_WELL]];
}


