
import React from 'react';
import './encounter-display.css'; // Assuming you have a CSS file for styling
import { Encounter } from '../models';

interface EncounterDisplayProps {
    encounter:Encounter;
}

const EncounterDisplay: React.FC<EncounterDisplayProps> = ({ encounter }) => {
    return (
        <div className={`encounter-display ${encounter.location.themes[0]}`}>
           <div className="encounter-name">
                {encounter.name}
            </div> 
        </div>
    );
};

export default EncounterDisplay;