import React from 'react';
import { Character } from '../models';
import Cimage from './UI/cimage';
import './characterCard.scss';
import { CLASS, RACE } from '../consts/general';


interface CharacterCardProps {
  character: Character;
  isSelected: boolean;
  onSelect: () => void;
  showAbilities: boolean;
  collapsed?: boolean;
  isAssigned?: boolean;
}

const CharacterCard: React.FC<CharacterCardProps> = ({ character, isSelected, onSelect, showAbilities, collapsed, isAssigned }) => {

  const getUFClass= (classId: CLASS)  =>{
    switch(classId) {
      case CLASS.BARBARIAN: return 'Barbarian';
      case CLASS.BARD: return 'Bard';
      case CLASS.CLERIC: return 'Cleric';
      case CLASS.DRUID: return 'Druid';
      case CLASS.FIGHTER: return 'Fighter';
      case CLASS.MONK: return 'Monk';
      case CLASS.PALADIN: return 'Paladin';
      case CLASS.RANGER: return 'Ranger';
      case CLASS.ROGUE: return 'Rogue';
      case CLASS.SORCERER: return 'Sorcerer';
      case CLASS.WIZARD: return 'Wizard';
      default: return 'Unknown';
    }  
  }

  const getUFRace= (raceId: RACE) => {
    switch(raceId) {
      case RACE.HUMAN: return 'Human';
      case RACE.ELF: return 'Elven';
      case RACE.DWARF: return 'Dwarven';
      case RACE.HALFLING: return 'Halfling';
      case RACE.HALF_ELF: return 'Half-Elf';
      case RACE.HALF_ORC: return 'Half-Orc';
      default: return 'Unknown';
    }
  }

  const getUFInitials= (character: Character) => {
    return character.name.split(' ').map((n) => n[0]).join('');
  }

  return (
   
    collapsed ? (
      <div className="character-card collapsed" onClick={onSelect}>
        <div className="character-icon">
          <Cimage imageId={character.icon} />
            <div className="character-initials">{getUFInitials(character)}</div>
        </div>
    </div>
    ) : (
    <div className={`character-card ${isSelected ? 'selected' : ''}`} onClick={onSelect}>
      <div className={`character-card-container ${isAssigned ? 'assigned' : ''} `}>
        <div className="character-card-header">
          <div className="character-info">
            <div className="character-name">{character.name}</div>
            <div className="character-details">
          <div className="character-race">{getUFRace(character.race)} {getUFClass(character.class)}</div>
            </div>
          </div>
          <div className="character-icon">
            <Cimage imageId={character.icon} />
            <div className="character-level">Level {character.level}</div>
          </div>
        </div>
        {showAbilities && (
          <div className="character-card-body">
            <div className="abilities-list">
              <ul>
                {character.abilities.map((ability, index) => (
                  <li key={index}>
                    <strong>{ability}</strong>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
        {isAssigned && <div className="character-assigned">Assigned</div>}

    </div>
    )

  );
};

export default CharacterCard;