import React, { useEffect } from 'react';
import './mainMenu.scss';
import AppManager from '../managers/appManager';
import { APP_STATE } from '../consts/states';
import Cimage from './UI/cimage';

interface MainMenuProps {
  appManager: AppManager; 
}

const MainMenuComponent: React.FC<MainMenuProps> = ({appManager}) => { 
  const handleNewGamePressed = () => {
    console.log('Option 1 selected');
    appManager.state = APP_STATE.PLAYER_RECRUITMENT;
  };

const disableAssignChars = () => {
  return '';//'disabled';
}

  const handleAssignCharacters = () => {
    appManager.state = APP_STATE.CHARACTER_CREATION;
  };

  const handleCampaignDesignerPressed = () => {
    appManager.state = APP_STATE.CAMPAIGN_SETUP;
  };

  const handleEditorPressed = () => {
    console.log('Option 4 selected');
  };

  const handleSettingsPressed = () => {
    console.log('Option 5 selected');
  };

  const handleExit = () => {
    console.log('Exit selected');
    window.location.href = 'https://www.google.com';
  };

  return (
    <div className="main-menu-container">
    
      <div className="main-menu-content">
        <div className="main-menu-title">MAIN MENU</div>
        <div className="main-menu-options">
          <button className="mm-button players" onClick={handleNewGamePressed}>Recruit Players</button>
          <button className={`mm-button characters ${disableAssignChars()}`} onClick={handleAssignCharacters}>Assign Characters</button>
          <button className="mm-button campaign-design" onClick={handleCampaignDesignerPressed}>Campaign Designer</button>
          <button className="mm-button disabled" onClick={handleSettingsPressed}>Begin</button>
        </div>
        <button className="exit-button" onClick={handleExit}>Exit</button>
      </div>
    </div>
  );
};

export default MainMenuComponent;
