import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import './playerCard.scss';
import { Player } from '../models';
import { FEEL } from '../tags';


interface PlayerPreferencesProps {
  player: Player;
  expanded: boolean;
}

const PlayerCard: React.FC<PlayerPreferencesProps> = ({ player, expanded}) => {
  const preferencesWindowRef = useRef<HTMLDivElement>(null);

  // TODO: come back to this! Keep these from going offscreen
  // useLayoutEffect(() => {
  //   if (expanded && preferencesWindowRef.current) {
  //     setTimeout(() => {
  //       if (preferencesWindowRef.current) {
  //         const rect = preferencesWindowRef.current.getBoundingClientRect();
  //         const windowHeight = window.innerHeight;
  //         const windowWidth = window.innerWidth;

  //         if (rect.bottom > windowHeight) {
  //           preferencesWindowRef.current.style.top = `${windowHeight - rect.height}px`;
  //         }
  //         if (rect.top < 0) {
  //           preferencesWindowRef.current.style.top = `0px`;
  //           console.log("FIX I")
  //         }
  //         if (rect.right > windowWidth) {
  //           preferencesWindowRef.current.style.left = `${windowWidth - rect.width}px`;
  //         }
  //         console.log(rect, windowHeight, windowWidth);
  //       }
  //     }, 1000);
  //   }
  // }, [expanded]);

  return (
    <div className={`player-preferences`}>
      {expanded ? 
      <div className="player-card-header expanded">
        <div className="preferences-window"  ref={preferencesWindowRef}>
            {player.preferences.map((pref, idx) => {
              return (
                <div
                  key={idx}
                  className="preference-pill"
                  style={{ backgroundColor: FEEL[pref[1]].color }}
                >
                  {pref[0]}
                </div>
              );
            })}
          </div>
      </div> : 
      <div className="player-card-compact">
         <div className="preferences-dots">
            {(player.preferences.length &&
             player.preferences.map((preference, idx) => {
              return (
                <div
                  key={idx}
                  className="preference-dot"
                  style={{ backgroundColor: FEEL[preference[1]].color }}
                  title={preference[0]}
                />
              );
            })) || null} 
      </div>
      </div>}
    </div>
  );
}

export default PlayerCard;