import React, { useEffect } from 'react';
import './globalUi.scss';
import { COLORS } from '../../consts/general';
import { getStateColor } from '../../utils/utils';
import { APP_STATE } from '../../consts/states';
import PlayerBar from './playerBar';
import { useAppManager, useCharacterManager, useDataManager, usePlayerManager } from '../managerContexts';

interface GlobalUiProps {
 
}

const GlobalUi: React.FC<GlobalUiProps> = () => {

 const appManager = useAppManager();
  const dataManager = useDataManager();
  const characterManager = useCharacterManager();
  const playerManager = usePlayerManager();

  const [state, setState] = React.useState(appManager.state);
  const [backgroundColor, setBackgroundColor] = React.useState(COLORS.STATE.MAIN_MENU)
  const [nextStateColor, setNextStateColor] = React.useState(COLORS.STATE.PLAYER_RECRUITMENT)
  const [showPlayerBar, setShowPlayerBar] = React.useState(false)
  const [numSelectedCharacters, setNumSelectedCharacters] = React.useState(0)
 
   

  useEffect(() => {
    const handleStateChange = () => {
      setState(appManager.state);
      updateState(appManager.state);
    };

    appManager.on('stateChange', handleStateChange);
    return () => {
      appManager.off('stateChange', handleStateChange);
    };
  
  }, [appManager]);

  useEffect(() => {
    setNumSelectedCharacters(characterManager.getSelectedCharacters()?.length || 0)
  }, [characterManager])
  
  function getStateComponent( state: APP_STATE) {
    switch(state) {
      case APP_STATE.PLAYER_RECRUITMENT:
        return getPlayerRecruitmentUi();
      case APP_STATE.CHARACTER_CREATION:
        return getCharacterCreationUi();
      case APP_STATE.MAIN_MENU:
      case APP_STATE.CAMPAIGN_SETUP:
      case APP_STATE.SESSION_PREP:
      case APP_STATE.SESSION:
      case APP_STATE.SCORE:
      default:
        return  getDefaultStateComponent()
    }
  }

  // not ytet implemented state UI
  function getDefaultStateComponent() {
    return <div></div>
  }

  function updateState(state: APP_STATE) {
    if(!dataManager) {return}
    const stateProps = dataManager.getStateProps(state)
    const newModeColor = getStateColor(state)
    setBackgroundColor(newModeColor)
    const nextModeColor = getStateColor(stateProps.nextState)
    setNextStateColor(nextModeColor)
    setShowPlayerBar(stateProps.ui.showCharacterBar)
  }

  function getPlayerRecruitmentUi() {
    return (
   <div></div>)
  }

  function getCharacterCreationUi() {
    return (
     <div>
      </div>
    )
  }

  return (
    <div className="global-ui-container">
      {/* <button
          className="next-button"
          style={{backgroundColor: nextStateColor}}
          onClick={() => appManager.advanceState()}
      >
            Advance To: {appManager.getDataManager().getStateProps().nextState}
      </button>    */}
      <div className="state-display" style={{backgroundColor: backgroundColor}}>
        {getStateComponent(state)}
      </div>
      {showPlayerBar ? 
        <PlayerBar
          playerManager={appManager.getPlayerManager()}
          appManager={appManager}  
        /> : null}
</div>
  );
}

export default GlobalUi;
